@import '../../../assets/css/theme.scss';
@import '../../../assets/css/var.scss';
// 第二阶段矿池样式
.farm_flex {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
}

.farm_card_flex {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  button {
    margin-top: 12px;
  }
}

.farm_margin {
  > div:nth-child(1) {
    margin: 0 10px 0 0;
  }
  > div:nth-child(3) {
    margin: 0 0 0 10px;
  }
}

.liquidity_card {
  background: $color-info-bg;
  border-radius: 10px;
  color: $white;
  padding: 0 20px 0 20px;
  margin-top: 20px;
}

// 矿池第一行
.liq_title {
  display: flex;
  justify-content: space-between;
  padding: 20px 0;
  border-bottom: solid 1px rgba($color: $white, $alpha: 0.1);
}

.speed_info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  .speed_info_tip {
    color: rgba(255, 255, 255, 0.5);
    margin-right: 8px;
    font-size: 12px;
  }
}

// 矿池第二行
.liq_info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.farm_status3 {
  &_top {
    display: flex;
    align-items: flex-start;
    img {
      width: 75px;
      height: 75px;
      margin-right: 12px;
    }
  }
  &_bottom {
    margin-top: 16px;
  }
  &_text1 {
    font-size: 14px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $white;
  }
  &_text2 {
    font-size: 12px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: rgba($color: $white, $alpha: 0.7);
  }
  &_text3 {
    font-size: 16px;
    font-family: Helvetica-Bold, Helvetica;
    font-weight: bold;
    color: $white;
  }
}

.w_100 {
  width: 100%;
}

.w_60 {
  width: 60%;
}

.w_40 {
  width: 40%;
}

.pc_show {
  display: block;
}

.mobile_show {
  display: none;
}

@include mobile-only {
  .farm_flex {
    display: block;
  }

  .liquidity_card {
    padding: 0 10px 0 10px;
  }

  .farm_card_flex {
    flex-direction: column;
    > div {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
    }
    > div:nth-child(2) {
      margin-top: 16px;
    }
    button {
      margin-top: 0;
    }
  }

  .liq_status3_card {
    flex-direction: column;
  }

  .liq_status3 {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .farm_margin {
    > div:nth-child(1) {
      margin: 0 0 10px 0;
    }
    > div:nth-child(3) {
      margin: 10px 0 0 0;
    }
  }

  .liq_title {
    flex-direction: column;
    border-bottom: none;
  }
  .speed_info {
    display: block;
    .speed_info_tip {
      font-size: 12px;
    }
  }
  .liq_info {
    flex-direction: column;
    border-bottom: solid 1px rgba($color: $white, $alpha: 0.1);
    padding-bottom: 16px;
    margin-bottom: 12px;
  }

  .pc_show {
    display: none;
  }

  .mobile_show {
    display: block;
  }
}
