@import '../../../../assets/css/theme';
@import '../../../../assets/css/var';

.pool_apply {
  min-height: 200px;
  background: url('../../../../assets/images/new-farm/farm_bg.png') no-repeat;
  border-radius: 10px;
  padding: 30px;
  position: relative;

  > p:nth-child(1) {
    -webkit-text-stroke: 1px rgba($color: $white, $alpha: 0.5);
    -webkit-text-fill-color: transparent;
    font-family: Helvetica-Bold, Helvetica;
    font-size: 32px;
    font-weight: 600;
    color: transparent;
    position: relative;
    > p {
      background-image: linear-gradient(to bottom, $white, #c79eff);
      -webkit-background-clip: text;
      color: transparent;
      font-family: Helvetica-Bold, Helvetica;
      font-size: 32px;
      font-weight: 600;
      position: absolute;
      top: -2px;
      left: -2px;
      z-index: 1;
    }
  }
  .pool_apply_text {
    margin-top: 20px;
    span {
      font-size: 16px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: $white;
    }
  }
  .pool_apply_img {
    position: absolute;
    right: 0;
    bottom: 0;
    height: 136px;
  }
}
@include mobile-only {
  .pool_apply {
    .btn_list {
      display: flex;
      flex-direction: column;
      width: 150px;
    }
  }
}
