.modal-text {
  position: relative;
  padding: 16px 20px;
  border-radius: 10px;
  .title {
    font-family: 'PingFang HK', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
      'WenQuanYi Micro Hei', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
  }
  .detail {
    font-family: 'PingFang SC', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
      'WenQuanYi Micro Hei', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    text-align: justify;
    margin-top: 10px;
  }
  &::after {
    content: '';
    display: block;
    position: absolute;
    bottom: 0;
    right: 0;
    background-repeat: no-repeat;
    background-size: 100%;
  }
}

.flexible-text {
  background: #fff7e8;
  .title {
    color: #e0a22e;
  }
  .detail {
    max-width: 210px;
    width: 76%;
    color: #dda234;
  }
  &::after {
    width: 135px;
    height: 160px;
    background-image: url(../images/new-farm/flexible_bg.png);
  }
}

.lock-text {
  background: #f8f8ff;
  .title {
    color: #833bde;
  }
  .detail {
    max-width: 210px;
    width: 76%;
    color: #9871c8;
  }
  &::after {
    width: 134px;
    height: 132px;
    background-image: url(../images/new-farm/lock_bg.png);
  }
}

.unlock-text {
  background: linear-gradient(272.58deg, #fff4f0 3.31%, #fffaf3 98.54%);
  .title {
    font-family: 'PingFang SC', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
      'WenQuanYi Micro Hei', sans-serif;
    font-weight: 500;
    font-size: 14px;
    color: #e66f2c;
  }
  .detail {
    width: 289px;
    color: #f19662;
    font-size: 12px;
    line-height: 17px;
  }
  &::after {
    width: 90px;
    height: 90px;
    background-image: url(../images/new-farm/unlock_bg.png);
  }
}

.claim-text {
  background: linear-gradient(272.58deg, #fffdf5 3.31%, #fffcef 98.54%);
  .title {
    font-family: 'PingFang SC', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
      'WenQuanYi Micro Hei', sans-serif;
    font-weight: 500;
    font-size: 14px;
    color: #e1b92b;
  }
  .detail {
    width: 289px;
    color: #dca925;

    font-size: 12px;
    line-height: 17px;
  }
  &::after {
    width: 90px;
    height: 90px;
    background-image: url(../images/new-farm/claim_bg.png);
  }
}

.common-text {
  font-family: 'PingFang SC', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
    'WenQuanYi Micro Hei', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 17px;
  color: #333333;
  > span > span {
    color: #5915e1;
  }
}

.mt-32 {
  margin-top: 32px;
}
.apy-summary {
  > div {
    margin-bottom: 5px;
    font-family: 'PingFang HK', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
      'WenQuanYi Micro Hei', sans-serif;
    font-weight: 400;
    font-size: 10px;
    line-height: 20px;
    color: #ffffff;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .tip-fix-apy {
    color: #b5a9ff;
  }
  .tip-flex-apy {
    color: #97cdff;
  }
  .tip-gov-apy {
    color: #cfe8ff;
  }
}

.unlock-ele {
  border-bottom: 1px solid rgba(229, 229, 229, 0.5);
  padding: 20px 0 10px;
  // margin-bottom: 20px;
  // &:last-child {
  //   margin-bottom: 0;
  // }
  &.claim-extend-ele {
    border-bottom: none;
    .ant-input-suffix {
      min-width: auto;
    }
  }
  .ant-input-affix-wrapper {
    border: none;
    padding-left: 0 !important;
    &.ant-input-affix-wrapper-disabled {
      cursor: initial !important;
    }
  }
  .ant-input {
    width: calc(100% - 160px);
    font-family: 'Helvetica Neue', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
      'WenQuanYi Micro Hei', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    color: #222222;
    &[disabled] {
      cursor: initial !important;
    }
  }
  .ant-input-suffix {
    min-width: 160px;
  }
  .unlock-part-btn {
    min-width: 78px;
    height: 24px;
    line-height: 24px;
    margin-top: 0;
    margin-left: 10px;
    font-family: 'Helvetica', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
      'WenQuanYi Micro Hei', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    color: #ffffff;
  }
  .unlock-suffix {
    width: 100%;
    justify-content: flex-end;
    > span {
      font-family: 'Helvetica Neue', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
        'WenQuanYi Micro Hei', sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 14px;
      color: #999999;
      white-space: nowrap;
    }
  }
  .title {
    font-family: 'PingFang SC', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
      'WenQuanYi Micro Hei', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 17px;
    color: #333333;
    margin-bottom: 12px;
  }
  .detail {
    .amount,
    .token {
      font-family: 'Helvetica Neue', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
        'WenQuanYi Micro Hei', sans-serif;
      font-style: normal;
      font-weight: 500;
      font-size: 20px;
      line-height: 24px;
      color: #222222;
    }
    .token {
      position: relative;
      top: -1px;
      font-weight: 400;
      font-size: 12px;
      line-height: 14px;
      color: #333;
      margin-left: 10px;
    }
  }
}

.claim-extend-ele {
  .ant-input,
  .ant-input-suffix {
    width: auto;
  }
  .ant-input {
    font-weight: 400;
    font-size: 12px;
  }
  .ant-input-affix-wrapper {
    padding-left: 10px !important;
  }
}

.lm-date {
  display: flex;
  flex-wrap: wrap;
  > span {
    display: inline-block;
    width: auto !important;
    min-width: 66px !important;
    padding: 0 5px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    border: 1px solid #e1e1e1;
    margin: 0 10px 10px 0;
    border-radius: 5px;
    font-size: 12px;
    color: #333;
    cursor: pointer;

    &.last {
      width: 370px !important;
      margin-right: 0 !important;
      padding: 0;
    }

    &:last-child {
      margin-right: 0;
    }

    &.current {
      border-color: #5915e1;
      color: #5915e1;
      .ant-input {
        color: #5915e1;
      }

      &.errorWeek {
        color: #ed635a;
        border-color: #ed635a;
        .ant-input {
          color: #ed635a;
        }
      }

      .ant-input {
        &::placeholder {
          color: #5915e1;
          opacity: 0.3;
        }
      }
    }
  }
  .ant-input-affix-wrapper {
    height: 28px;
    line-height: 28px;
    border: none;
  }
  .ant-input {
    text-align: center;
    width: 310px;
  }
  .ant-input-suffix {
    font-family: 'Helvetica', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
      'WenQuanYi Micro Hei', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    color: #333333;
  }
}

.lock-time {
  font-family: 'PingFang SC', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
    'WenQuanYi Micro Hei', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 17px;
  color: #333333;
}

.stakeTip1 {
  > span > span {
    color: #333333;
  }
}

.edit-icon {
  margin: 0 0 2px 10px;
}
.speed-to {
  > span > span {
    color: #ed635a;
  }
}
.lock-speed-tip {
  color: #bc9bff;
  text-decoration: underline;
  cursor: pointer;
}

.lm-date {
  &.new-lm-date > span:last-child {
    width: 60px !important;
  }
}

.emer-text {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  .title {
    font-family: 'PingFang SC', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
      'WenQuanYi Micro Hei', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 17px;
    color: #666666;
  }
  .detail {
    font-family: 'Helvetica', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
      'WenQuanYi Micro Hei', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    color: #999;
    .claim-amount {
      font-weight: 700;
      color: #333;
    }
    .lock-rewards {
      font-weight: 700;
      color: #999;
    }
  }
}
.emer-warning {
  display: flex;
  font-family: 'PingFang SC', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
    'WenQuanYi Micro Hei', sans-serif;
  align-items: flex-start;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 17px;
  color: #ed635a;
  margin-top: 30px;
  > img {
    margin-right: 5px;
    position: relative;
    top: 2px;
  }
}
.emer-btn {
  margin-top: 15px !important;
  margin-bottom: 6px;
}

.flex-apy {
  height: 63px;
}

.apy-new-type1 {
  border-bottom: 1px dashed #333333 !important;
}

.lock-time,
.extend-check {
  .ant-radio-group {
    display: inline-flex;
  }
  .ant-radio-wrapper {
    font-family: 'PingFang SC';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 17px;
    /* identical to box height */

    text-align: right;

    color: #333333;
    &:last-child {
      margin-right: 0;
      margin-left: 2px;
    }
  }
  .ant-radio-inner {
    width: 12px;
    height: 12px;
    border-color: #5915e1;
    top: -1px;
  }
  .ant-radio-checked .ant-radio-inner {
    border: 1px solid #5915e1;

    &::after {
      width: 6px;
      height: 6px;
    }
  }
}

.ant-modal.lock-modal {
  .ant-modal-body {
    padding: 20px;
  }
}

.ant-modal.auto-extend {
  .ant-modal-header {
    background-image: url(../images/new-farm/auto-extend-bg.png) !important;
  }
}

.ant-modal.unlock-claim-modal {
  .ant-modal-content {
    padding: 0;
  }
  .ant-modal-header {
    background-image: url(../images/new-farm/unlock-claim-bg.png) !important;
  }
}

@media screen and (max-width: 750px) {
  .flexible-text::after {
    width: 74px;
    height: 90px;
  }
  .lock-text::after {
    width: 74px;
    height: 90px;
  }

  .flexible-text,
  .lock-text {
    .detail {
      max-width: 80%;
    }
  }
}

@media screen and (max-width: 500px) {
  .lock-extend-content {
    .extend-btns {
      display: block;
      > div {
        width: 100%;
        text-align: center;
        &:first-child {
          margin-bottom: 10px;
        }
      }
    }
  }

  .lm-date {
    > span {
      min-width: 56px !important;
    }
  }
}
@media screen and (max-width: 400px) {
  .lm-date {
    > span {
      min-width: 48px !important;
    }
  }
}
