@import '../../../../assets/css/theme';
@import '../../../../assets/css/var.scss';

.farm_top_box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0;
  img {
    height: 60px;
    white-space: nowrap;
  }
  > span:nth-child(2) {
    margin-left: 20px;
  }
}

.farm_name {
  font-size: 20px;
  font-weight: 600;
  color: $white;
  margin-bottom: 8px;
}

.farm_subTitle {
  font-size: 14px;
  font-weight: 400;
  font-family: PingFangSC-Regular, PingFang SC;
  color: $white;
  margin-right: 20px;
  opacity: 0.5;
}

.version_icon {
  border-radius: 12px;
  padding: 3px 8px;
  font-size: 12px;
  color: #e44646;
  background: rgba(228, 70, 70, 0.1);
  margin-right: 6px;
}

.flex_icon {
  background: rgba(40, 202, 186, 0.1);
  border-radius: 12px;
  padding: 3px 8px;
  font-size: 12px;
  color: #28caba;
}

.lock_icon {
  background: rgba(255, 172, 0, 0.1);
  border-radius: 12px;
  padding: 3px 8px;
  font-size: 12px;
  color: #ffac00ff;
  margin-left: 8px;
}

@include mobile-only {
  .farm_name {
    font-size: 14px;
  }
  .farm_subTitle {
    font-size: 12px;
  }
  .farm_top_box {
    justify-content: flex-start;
    margin-bottom: 16px;
    img {
      height: 20px;
    }
    > span:nth-child(2) {
      margin-left: 10px;
    }
  }
}
