@import '../../../../assets/css/theme';
@import '../../../../assets/css/var.scss';

.switch_wrap {
  background: rgba($white, 0.1);
  border-radius: 17px;
  border: 1px solid rgba($white, 0.3);

  > span {
    display: inline-block;
    padding: 6px 15px;
    font-size: 12px;
    font-weight: 400;
    color: $white;
    cursor: pointer;
  }
}

.switch_active {
  background: $purple;
  border-radius: 16px;
}
